import './App.css';
import React from 'react'
import useContext from "./useContext"

function App(){
  
  const {text, handleChange, running, inputRef, time, startGame, count, btn,  pause, resume} = useContext()

  return (
    <div >
    <h1>Speed Typing Game</h1>
    <textarea 
      value={text}
      onChange={handleChange}
      disabled={!running}
      ref={inputRef}
      
    />
    <h4>Time remaining: {time}</h4>
    <button disabled={running} onClick={startGame}> {btn}</button>
    <button  onClick={resume}> {pause}</button>
    <h1>Word Count: {count} </h1>
  </div>
  )
   
  
}

export default App;
